.overlay{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(36,41,47,.3);z-index:1001;display:flex;justify-content:center;align-items:center}.overlay .container{border-radius:2rem;background-color:#fff;margin:auto;padding:4rem 3.5rem;display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative}.overlay .container .headingContainer{display:flex;align-items:center}.overlay .container .headingContainer .pin{margin:-1rem 0;margin-left:.5rem;width:4rem}.overlay .container p{font-size:3rem;font-weight:400;color:#24292f}.overlay .container .inputContainer{margin-top:2rem;display:flex;width:50rem;position:relative}.overlay .container .inputContainer .returnContainer{position:absolute;top:1.2rem;right:1rem;display:flex;align-items:center}.overlay .container .inputContainer .returnContainer p{font-size:2rem;color:#319fff;font-weight:100}.overlay .container .inputContainer .returnContainer .return_arrow{margin-left:.5rem;width:2.5rem}.overlay .container .inputContainer .wordCount{position:absolute;font-size:1.4rem;color:#757575;font-weight:200;top:-2rem;right:.5rem}.overlay .container .inputContainer input,.overlay .container .inputContainer textarea{text-align:center;color:#24292f;font-size:2rem;font-weight:300;padding:1rem 10rem;border:.15rem solid #319fff;border-radius:1rem;flex:auto;resize:none;font-family:"Josefin Sans",sans-serif}.overlay .container .inputContainer input[type=file],.overlay .container .inputContainer textarea[type=file]{display:none}.overlay .container .inputContainer input:focus,.overlay .container .inputContainer textarea:focus{outline:none}.overlay .container .inputContainer input::placeholder,.overlay .container .inputContainer textarea::placeholder{color:#989898}.overlay .container .inputContainer.uploadPFPContainer{flex-direction:column;align-items:center}.overlay .container .inputContainer.uploadPFPContainer .uploadPFP{cursor:pointer;background-color:#fafafa;width:13.3rem;aspect-ratio:1;border-radius:50%;border:.15rem solid #319fff;display:flex;justify-content:center;align-items:center;transition:.3s}.overlay .container .inputContainer.uploadPFPContainer .uploadPFP:hover{background-color:#f5f5f5}.overlay .container .inputContainer.uploadPFPContainer .account{width:10rem}.overlay .container .inputContainer.uploadPFPContainer button{margin-top:2rem;background-color:#319fff;padding:1rem 1.5rem;border-radius:1rem;font-size:2rem;color:#fff;font-weight:400;cursor:pointer}.overlay .container .warning{position:absolute;font-size:1.5rem;color:#c30000;bottom:1.25rem}